<template>

  <div class="project">
      <transition name="van-fade">
        <p v-show="!hideOther && !showingObject.name">()</p>
      </transition>
      <div class="project_list">
          <transition name="van-fade"  v-for="(item, index) in projectList" :key="index">
            <div class="project_item pub_flex_sb" v-show="(showingObject.name == item.name || !showingObject.name ) && !hideOther">
                <div class="item_msg left_msg" v-if="index%2==0">
                    <p class="name">{{item.name}}</p>
                    <p class="desc" v-html="item.desc"></p>
                </div>
                <div class="left_img pub_flex_center">
                    <img :src="item.icon" alt="">
                    <!-- <van-image
                        fit="cover"
                        width="2.4rem"
                        height="2.4rem"
                        :src="item.icon"
                    /> -->
                    <van-button color="#99CCCC" plain round size="small" style="margin-top:10px;" v-show="!showingObject.name" @click="projectDetail(item)"></van-button>
                    <van-button color="#99CCCC" plain round size="small" style="margin-top:10px;" v-show="showingObject.name" @click="viewObject(item)"></van-button>
                </div>
                <div class="item_msg right_msg" v-if="index%2==1">
                    <p class="name">{{item.name}}</p>
                    <p class="desc" v-html="item.desc"></p>
                </div>
            </div>
          </transition>
          <transition name="van-fade">
            <div class="detail" v-show="showingObject.name && !hideOther">
              <p v-for="(item, index) in showingObject.detail" :key="index"><van-icon name="arrow" />{{item}}</p>
              <van-button color="#99CCCC" plain round size="small" style="margin-top:10px;" @click="showAll"></van-button>
            </div>
          </transition>
      </div>
      <!-- <van-popup v-model="showDetail" class="detail_popup">{{showingObject}}</van-popup> -->
  </div>
</template>

<script>
// @ is an alias to /src

import Vue from "vue";
import { Swipe, SwipeItem, Image as VanImage, Button, Popup, Icon } from "vant";
import { ImagePreview } from "vant";
Vue.use(Icon);
Vue.use(Swipe);
Vue.use(SwipeItem);
Vue.use(VanImage);
Vue.use(Button);
Vue.use(Popup);

export default {
  name: "project",
  data() {
    return {
      // showDetail:false,
      showingObject: {},
      hideOther: false,
      projectList: [
        {
          icon: require("assets/images/8g7.png"),
          name: "",
          desc:
            "<br>",
          detail: [
            "",
            "vant-weapp",
            "",
            "",
            "canvans",
            "IM",
            ""
          ],
          imgList: [
            require("assets/images/8g7_qrcode.jpg"),
            require("assets/images/8g7_img1.jpg"),
            require("assets/images/8g7_img2.jpg"),
            require("assets/images/8g7_img3.jpg"),
            require("assets/images/8g7_img4.jpg")
          ]
        },
        {
          icon: require("assets/images/anst.png"),
          name: "()",
          desc:
            "<br>",
          detail: [
            "",
            "bug()",
            "vue+webpack+ant-design-vue",
            "router",
            "vuex",
            "antvvue",
            "",
            "ExportJsonExcelexcel",
            "......"
          ],
          imgList: [
            require("assets/images/anst_qrcode.png"),
            require("assets/images/anst_img1.jpg"),
            require("assets/images/anst_img2.jpg"),
            require("assets/images/anst_img3.jpg"),
            // require("assets/images/anst_img4.jpg"),
          ]
        },
        {
          icon: require("assets/images/wzyf.png"),
          name: "",
          desc:
            ""
        }
      ]
    };
  },
  components: {},
  mounted() {},
  methods: {
    //
    projectDetail(item) {
      this.hideOther = true;

      setTimeout(() => {
        this.showingObject = item;
        this.showDetail = true;
        this.hideOther = false;
        // console.log(this.showDetail);
      }, 500);
    },
    showAll() {
      this.hideOther = true;
      setTimeout(() => {
        this.showingObject = {};
        this.hideOther = false;
      }, 500);
    },
    //
    viewObject(item) {
      ImagePreview({
        images: item.imgList,
        closeable: true,
        onClose() {
          // do something
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
.project {
  width: 100%;
  text-align: center;
  .detail_popup {
    color: #333;
    border-radius: 20px;
    padding: 30px;
    width: 670px;
    box-sizing: border-box;
  }
}
.project_list {
  margin: 60px auto;
  transition: all 0.3s;

  .project_item {
    text-align: left;
    transition: all 0.3s;
    padding-right: 30px;
    box-sizing: border-box;
    padding-top: 30px;
    align-items: flex-start;
    .left_img {
      flex-shrink: 0;
      width: 160px;
      height: 100%;
      flex-flow: column;
      padding-top: 30px;
      img {
        height: 70px;
        width: auto;
        flex-shrink: 0;
      }
    }
    .name {
      text-align: center;
      padding-bottom: 20px;
    }
    .desc {
      font-size: 26px;
    }
    .left_msg {
      padding-left: 30px;
    }
    .right_msg {
      padding-right: 30px;
    }
  }
  .detail {
    font-size: 28px;
    padding: 40px;
    text-align: left;
    line-height: 42px;
  }
}
</style>
